import {
  Box,
  Button,
  Collapse,
  IconButton,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@material-ui/core'
import { ArrowDropDown, ArrowDropUp } from '@material-ui/icons'
import React, { useState } from 'react'
import { Show, ShowProps, useShowContext, useTranslate } from 'react-admin'
import { useSelector } from 'react-redux'
import {
  BlockOverallView,
  ObjectOverallView,
  TribuneOverallView,
} from '../../../core/stats/overall-view/overall-view'
import { reducerType } from '../../../core/common/theme.reducer'

const useStyles = makeStyles({
  noBorderCell: {
    borderBottom: 'none',
  },
})

const ViewTitle = (record: any) => {
  const {
    record: { eventName, date, id },
  } = record
  const t = useTranslate()
  const description = `${eventName} ${date.replace('T', ' ')} ${id}`
  return (
    <span>{`${t('resources.stats-overall-view.name')}: ${description}`}</span>
  )
}

const BlockView = (block: BlockOverallView) => {
  const {
    capacity,
    entered,
    enteredAbo,
    enteredNormal,
    enteredInfix,
    enteredPoints,
    enteredService,
    name,
    sold,
    soldAbo,
    soldNormal,
    soldInfix,
    soldPoints,
    soldService,
  } = block
  const [open, setOpen] = useState(false)
  const classes = useStyles()
  const backColor = useSelector((state: reducerType) =>
    state.theme.darkMode === 'light' ? '#FAFAFA' : '#7A7A7A',
  )

  return (
    <>
      <TableRow style={{ backgroundColor: backColor }}>
        <TableCell classes={{ root: open ? classes.noBorderCell : undefined }}>
          {sold > 0 && (
            <IconButton
              onClick={() => setOpen(!open)}
              style={{ height: '40px', width: '40px', marginLeft: '30px' }}
            >
              {open ? <ArrowDropUp /> : <ArrowDropDown />}
            </IconButton>
          )}
          <span style={{ marginLeft: sold > 0 ? '2px' : '72px' }}>{name}</span>
        </TableCell>
        <TableCell
          style={{ textAlign: 'center' }}
          classes={{ root: open ? classes.noBorderCell : undefined }}
        >
          {entered}
        </TableCell>
        <TableCell
          style={{ textAlign: 'center' }}
          classes={{ root: open ? classes.noBorderCell : undefined }}
        >
          {sold}
        </TableCell>
        <TableCell
          style={{ textAlign: 'center' }}
          classes={{ root: open ? classes.noBorderCell : undefined }}
        >
          {capacity}
        </TableCell>
      </TableRow>
      {open && (
        <TableRow style={{ backgroundColor: backColor }}>
          <TableCell>
            {soldNormal > 0 ? (
              <div style={{ marginLeft: '71px' }}>NORMAL</div>
            ) : (
              <div />
            )}
            {soldAbo > 0 ? (
              <div style={{ marginLeft: '71px' }}>ABO</div>
            ) : (
              <div />
            )}
            {soldInfix > 0 ? (
              <div style={{ marginLeft: '71px' }}>INFIX</div>
            ) : (
              <div />
            )}
            {soldPoints > 0 ? (
              <div style={{ marginLeft: '71px' }}>POINTS</div>
            ) : (
              <div />
            )}
            {soldService > 0 ? (
              <div style={{ marginLeft: '71px' }}>SERVICE</div>
            ) : (
              <div />
            )}
          </TableCell>
          <TableCell>
            <div style={{ textAlign: 'center' }}>
              {soldNormal > 0 ? enteredNormal : ''}
            </div>
            <div style={{ textAlign: 'center' }}>
              {soldAbo > 0 ? enteredAbo : ''}
            </div>
            <div style={{ textAlign: 'center' }}>
              {soldInfix > 0 ? enteredInfix : ''}
            </div>
            <div style={{ textAlign: 'center' }}>
              {soldPoints > 0 ? enteredPoints : ''}
            </div>
            <div style={{ textAlign: 'center' }}>
              {soldService > 0 ? enteredService : ''}
            </div>
          </TableCell>
          <TableCell>
            <div style={{ textAlign: 'center' }}>
              {soldNormal > 0 ? soldNormal : ''}
            </div>
            <div style={{ textAlign: 'center' }}>
              {soldAbo > 0 ? soldAbo : ''}
            </div>
            <div style={{ textAlign: 'center' }}>
              {soldInfix > 0 ? soldInfix : ''}
            </div>
            <div style={{ textAlign: 'center' }}>
              {soldPoints > 0 ? soldPoints : ''}
            </div>
            <div style={{ textAlign: 'center' }}>
              {soldService > 0 ? soldService : ''}
            </div>
          </TableCell>
          <TableCell />
        </TableRow>
      )}
    </>
  )
}

const TribuneView = (tribune: TribuneOverallView) => {
  const { capacity, entered, name, sold, blocks } = tribune
  const [open, setOpen] = useState(false)

  return (
    <>
      {blocks.length > 0 && capacity > 0 && (
        <>
          <TableRow>
            <TableCell>
              <IconButton
                onClick={() => setOpen(!open)}
                style={{ height: '40px', width: '40px' }}
              >
                {open ? <ArrowDropUp /> : <ArrowDropDown />}
              </IconButton>
              <span style={{ marginLeft: '2px' }}>{name}</span>
            </TableCell>
            <TableCell style={{ textAlign: 'center' }}>{entered}</TableCell>
            <TableCell style={{ textAlign: 'center' }}>{sold}</TableCell>
            <TableCell style={{ textAlign: 'center' }}>{capacity}</TableCell>
          </TableRow>
          {open &&
            blocks.map((block) => <BlockView {...block} key={block.id} />)}
        </>
      )}
    </>
  )
}

const ObjectView = (object: ObjectOverallView) => {
  const { capacity, entered, name, sold, tribunes } = object
  const [open, setOpen] = useState(true)

  return (
    <Box
      style={{ margin: '5px', borderRadius: '5px', boxShadow: '0px 0px 2px' }}
    >
      <Button style={{ width: '100%' }} onClick={() => setOpen(!open)}>
        {name}
      </Button>
      <Collapse in={open}>
        <TableContainer>
          <Table padding="none">
            <TableHead>
              <TableRow>
                <TableCell style={{ width: '25%' }}>
                  <div style={{ marginLeft: '2px' }}>Trybuna</div>
                </TableCell>
                <TableCell style={{ width: '25%', textAlign: 'center' }}>
                  Wejścia
                </TableCell>
                <TableCell style={{ width: '25%', textAlign: 'center' }}>
                  Sprzedano
                </TableCell>
                <TableCell style={{ width: '25%', textAlign: 'center' }}>
                  Pojemność
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {tribunes.map((tribune) => (
                <TribuneView {...tribune} key={tribune.id} />
              ))}
              <TableRow style={{ height: '40px' }}>
                <TableCell>
                  <div style={{ marginLeft: '2px' }}>Suma</div>
                </TableCell>
                <TableCell style={{ textAlign: 'center' }}>{entered}</TableCell>
                <TableCell style={{ textAlign: 'center' }}>{sold}</TableCell>
                <TableCell style={{ textAlign: 'center' }}>
                  {capacity}
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
      </Collapse>
      {!open && (
        <Table padding="none">
          <TableHead>
            <TableRow>
              <TableCell style={{ width: '25%' }} />
              <TableCell style={{ width: '25%', textAlign: 'center' }}>
                Wejścia
              </TableCell>
              <TableCell style={{ width: '25%', textAlign: 'center' }}>
                Sprzedano
              </TableCell>
              <TableCell style={{ width: '25%', textAlign: 'center' }}>
                Pojemność
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            <TableRow style={{ height: '40px' }}>
              <TableCell>
                <div style={{ marginLeft: '2px' }}>Suma</div>
              </TableCell>
              <TableCell style={{ textAlign: 'center' }}>{entered}</TableCell>
              <TableCell style={{ textAlign: 'center' }}>{sold}</TableCell>
              <TableCell style={{ textAlign: 'center' }}>{capacity}</TableCell>
            </TableRow>
          </TableBody>
        </Table>
      )}
    </Box>
  )
}

const StatsView = () => {
  const { record } = useShowContext()

  return (
    <>
      {record?.objects.map((object: ObjectOverallView) => (
        <ObjectView {...object} key={record.id} />
      ))}
    </>
  )
}

export const OverallView = (props: ShowProps) => (
  <Show title={<ViewTitle />} {...props}>
    <StatsView />
  </Show>
)
